import React from "react";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Avatar from "@material-ui/core/Avatar";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {} from "@fortawesome/free-solid-svg-icons";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import CardHeader from "@material-ui/core/CardHeader";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardMedia from "@material-ui/core/CardMedia";
import IconButton from "@material-ui/core/IconButton";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";

import {
  faFacebookSquare,
  faLinkedinIn,
  faInstagram,
  faTwitterSquare,
  faGithubSquare
} from "@fortawesome/free-brands-svg-icons";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: theme.palette.background.paper
    // width: 500,
  }
}));

export default function MyTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  function handleChange(event, newValue) {
    setValue(newValue);
  }

  function handleChangeIndex(index) {
    setValue(index);
  }

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label="ME" {...a11yProps(0)} />
          <Tab label="PROJECTS" {...a11yProps(1)} />
          <Tab label="CERTIFICATES" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === "rtl" ? "x-reverse" : "x"}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <Container maxWidth="lg">
            <Grid
              spacing={3}
              container
              direction="column"
              alignItems="center"
              justify="center"
            >
              <Grid item sm={12} lg={3} xl={3}>
                <Paper
                  className={classes.paper}
                  style={{
                    paddingTop: "16px",
                    paddingBottom: "16px",
                    paddingLeft: "16px",
                    paddingRight: "16px"
                  }}
                >
                  <Avatar
                    alt="Shubham Chhimpa"
                    src="/me.jpg"
                    style={{
                      width: "200px",
                      height: "200px",
                      marginLeft: "22px"
                    }}
                  />
                  <Grid item sm={12} lg={12} xl={12}>
                    <FontAwesomeIcon
                      style={{
                        margin: "16px"
                      }}
                      icon={faLinkedinIn}
                      size="2x"
                    />
                    <FontAwesomeIcon
                      style={{
                        margin: "16px"
                      }}
                      icon={faGithubSquare}
                      size="2x"
                    />
                    <FontAwesomeIcon
                      style={{
                        margin: "16px"
                      }}
                      icon={faTwitterSquare}
                      size="2x"
                    />

                    <FontAwesomeIcon
                      style={{
                        margin: "16px"
                      }}
                      icon={faFacebookSquare}
                      size="2x"
                    />
                  </Grid>
                </Paper>
              </Grid>
            </Grid>
            <Grid
              spacing={3}
              container
              alignItems="center"
              direction="column"
              justify="center"
            >
              <Grid
                item
                xm={12}
                sm={4}
                lg={12}
                xl={12}
                container
                alignItems="center"
                direction="column"
                justify="center"
              >
                <Box width={1 / 2} minWidth={300}>
                  <Card style={{ textAlign: "center" }}>
                    <CardHeader title="ABOUT ME" />
                    <CardContent>
                      <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                      >
                        My interest towards computer science sparked when I was
                        working on a women's safety application aimed towards
                        the women welfare in India and since then I have been
                        incessantly working on improving my skills. I stumbled
                        upon machine learning then. I have made several
                        open-source contributions in the field of ML and would
                        continue to do so. I have made successful prototypes
                        like autonomous car, alphabet recognition, cancer
                        classification, gesture recognition using learning
                        models. These prototypes showcase the power of deep
                        learning and how I can help your organization implement
                        learning models to solve business cases. My ambition is
                        to make valuable contribution towards ML community and
                        leave a message of perseverance and tenacity. Currently,
                        I am working as a software engineer at Symantec, India.
                        I am also deeply influenced by literature especially
                        Shakespeare's work
                      </Typography>
                    </CardContent>
                  </Card>
                </Box>
              </Grid>
            </Grid>
          </Container>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <Container maxWidth="lg">
            <Grid
              spacing={3}
              container
              alignItems="center"
              direction="column"
              justify="center"
            >
              <Grid
                item
                xm={12}
                sm={4}
                lg={12}
                xl={12}
                container
                alignItems="center"
                direction="column"
                justify="center"
              >
                <Box width={1 / 2} minWidth={300}>
                  <Card style={{ textAlign: "center", padding: "0" }}>
                    <CardHeader title="ABOUT ME" />
                    <Box width={1} minWidth={300}>
                      <div
                        className="video"
                        style={{
                          position: "relative",
                          paddingBottom: "56.25%" /* 16:9 */,
                          paddingTop: 25,
                          height: 0
                        }}
                      >
                        <iframe
                          style={{
                            position: "absolute",
                            top: 0,
                            left: 0,
                            width: "100%",
                            height: "100%"
                          }}
                          title="h"
                          src={`https://www.youtube.com/embed/ugZ3myVmDAc`}
                          frameBorder="0"
                          allowFullScreen
                        />
                      </div>
                    </Box>
                    <CardContent>
                      <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                      >
                        My interest towards computer science sparked when I was
                      </Typography>
                    </CardContent>
                    <CardActions disableSpacing>
                      <IconButton aria-label="add to favorites">
                        <FontAwesomeIcon
                          // style={{
                          //   margin: "16px"
                          // }}
                          icon={faGithubSquare}
                          size="lg"
                        />{" "}
                      </IconButton>
                    </CardActions>
                  </Card>
                </Box>
              </Grid>
            </Grid>
          </Container>
        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
          Item Three
        </TabPanel>
      </SwipeableViews>
    </div>
  );
}
